/* Home */
body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Dosis", sans-serif;
}

.navbar {
  font-family: "Dosis", sans-serif;
  border-radius: 0px;
  margin-bottom: 0px;
  min-height: 100px;
}

.featuredContainerText {
  font-family: "Dosis", sans-serif;
  font-size: 36px;
  font-weight: 100;
  line-height: 1.4em;
}

.button-link {
  font-family: "Dosis", sans-serif;
  border: 2px solid #f60a4d;
  border-radius: 15px;
  color: #f60a4d;
  display: inline-block;
  font-weight: 700;
  margin: auto 15px;
  margin-top: 50px;
  margin-bottom: 100px;
  padding: 15px 20px;
  text-transform: uppercase;
  transition: all ease-out .2s;
  text-decoration: none;
}

.button-link:hover {
  text-decoration: none;
  color: white;
  background: #f60a4d;
  transition: all ease-out .2s;
}

.seoContent {
  font-family: "Dosis", sans-serif;
  font-size: 40px;
}

.navbar-default {
  background: #0f0f0f;
  border: none;
  box-shadow: none;
  -webkit-box-shadow: none;
}

