  color: #9a9a9a;
  text-decoration: none;
}

.alterPages li a:hover {
  color: white;
  transition: all ease-out .2s;
}

.alterPages li:after {
  content: '/';
  margin-left: 10px;
}

.alterPages li:last-child:after {
  content: '';
}

.alterPages li.active a {
  color: white;
  transition: all ease-out .2s;
}

.gameList {
  margin-top: -50px;
}

